<script>
  export default {
    name: 'Page',
    data: function () {
      return {}
    },
    mounted: function () {
      // eslint-disable-next-line
      componentHandler.upgradeDom()
      // eslint-disable-next-line
      componentHandler.upgradeAllRegistered()
      this.$root.setSearch(!!this.displaySearch)
      this.$root.setBack(!!this.displayBack)
    }
  }
</script>



